body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-linear-gradient(left, #86d876 0%,#7ae76c 0%,#f4d24c 100%,#ebeb17 100%);
  background: linear-gradient(to right, #86d876 0%,#7ae76c 0%,#f4d24c 100%,#ebeb17 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.wrapper {
    padding: 50px;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-content: flex-start;
            align-content: flex-start;
    overflow: auto;
    max-width: 1000px;
  }
  
.card {
    background:rgb(100, 55, 236);
    height: 200px;
    width: 200px;
    margin-top: 1rem;
    position: relative;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  }
  .card > .img-container {
    overflow: hidden;
    text-align: center;
  }
  .card > .img-container > img {
    width: 60%;
  }

 .Leela.Centaur {
    padding-top: 2rem;
  }

  .Roberto,
  .Bender.Angry,
  .Female.Fry {
    padding-top: 1rem;
  }

  .Nibbler,
  .Zoidberg,
  .Bender.Parties,
  .Zap  {
    padding-top: 0.5rem;
  }
.score-wrapper {
    width: 100%;
}

.btn {
    width: 25%;
    font-weight: bold;
    box-shadow: 0 3px 6px #999;
}

.btn-score {
    background-color:rgb(223, 220, 32);
}

.btn-topScore {
    background-color:rgb(9, 181, 255);
    color: white;
}

.btn-title {
    background-color:rgb(100, 55, 236);
    width: 50% !important;
    color: white;
    box-shadow: 0 -8px 6px #999, 0 8px 6px #999;
}
